.landing-root {
  height: 100vh;
  width: 100%;
  overflow: scroll;
  background: #000;
  display: flex;
  flex-direction: column;
}
.landing-header {
  padding: 25px 10%;
  display: flex;
  flex-direction: row;
  z-index: 2;
}
.landing-header .landing-header-nav-wrap {
  margin-left: auto;
  display: flex;
  flex-direction: row;
  align-items: center;
  cursor: default;
}
.landing-header .landing-header-nav-wrap .landing-header-nav-text {
  font-family: Montserrat Medium;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  color: #fff;
}
.landing-header-poper {
  margin-top: 10px;
  border-radius: 10px;
  overflow: hidden;
  z-index: 100;
}
@supports ((-webkit-backdrop-filter: none) or (backdrop-filter: none)) {
  .landing-header-poper {
    background: rgba(0, 0, 0, 0.2);
    -webkit-backdrop-filter: blur(100px);
    backdrop-filter: blur(100px);
  }
}
@supports not ((-webkit-backdrop-filter: none) or (backdrop-filter: none)) {
  .landing-header-poper {
    background: #181b1c;
  }
}
.landing-header-poper .tooltip {
  display: flex;
  flex-direction: column;
}
.landing-header-poper .tooltip .tooltip-item {
  padding: 13px 20px;
  align-items: center;
  display: flex;
}
.landing-header-poper .tooltip .tooltip-item:hover {
  background: hsla(0, 0%, 100%, 0.1);
}
.landing-header-poper .tooltip .tooltip-item p {
  margin-left: 10px;
  font-family: Montserrat SemiBold;
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 20px;
  color: #fff;
}
.landing-footer {
  margin-top: auto;
  padding: 32px 15%;
  z-index: 2;
}
.landing-footer .landing-footer-item-wrap {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.landing-footer .landing-footer-item-wrap .landing-footer-item-title {
  font-family: Montserrat;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  color: #fff;
  margin-bottom: 4px;
}
